  border: 2px solid #ed0233;
  background-color: transparent; }

.timetable__table-day-classes-list .timetable__table-day--active {
  background-color: #ed0233;
  border: 2px solid #ed0233;
  color: #ffffff; }

.timetable__table-time-list .timetable__table-time--active {
  background-color: #ed0233;
  border: 2px solid #ed0233;
  color: #ffffff; }

.timetable__table-time-overley {
  display: none; }

@media (max-width: 1199px) {
  .timetable {
    padding-top: 80px;
    padding-bottom: 80px; }
  .timetable__wrapper h2 {
    font-size: 42px;
    line-height: 42px; }
  .timetable__table {
    margin-top: 50px; }
  .timetable__table-day-classes {
    overflow-x: auto;
    padding-bottom: 8px; }
  .timetable__table-day-classes::-webkit-scrollbar {
    width: 100%;
    height: 4px; }
  .timetable__table-day-classes::-webkit-scrollbar-track {
    background-color: #feebef; }
  .timetable__table-day-classes::-webkit-scrollbar-thumb {
    background-color: #ed0233; }
  .timetable__table-day-classes-list {
    margin-right: 8px; }
  .timetable__table-time-list li {
    padding: 48px 13px; }
  .timetable__table-day {
    max-width: 146px; } }

@media (max-width: 767px) {
  .timetable {
    padding-top: 70px;
    padding-bottom: 70px; }
  .timetable__wrapper h2 {
    font-size: 32px;
    line-height: 32px; }
  .timetable__table-time {
    position: relative; }
  .timetable__table-time-list li {
    padding: 35px 12px;
    font-size: 16px;
    line-height: 16px; }
  .timetable__table-day-classes {
    flex-direction: column;
    justify-content: flex-start; }
  .timetable__table-day-classes-list {
    display: none;
    margin-right: 0; }
  .timetable__table-day-classes-list--active {
    display: block; }
  .timetable__table-day-classes-list li {
    font-size: 16px;
    line-height: 18px;
    max-width: none;
    width: calc(100% - 50px); }
  .timetable__table-day {
    position: relative; }
  .timetable__table-day-classes-list--active .timetable__table-day::after {
    display: flex;
    position: absolute;
    content: url("../img/arrow-down.svg");
    right: -52px;
    top: 50%;
    transform: translateY(-50%);
    width: 52px;
    height: 58px;
    border: 2px solid #1c3374;
    justify-content: center;
    align-items: center; }
  .timetable__table-day-classes-list--active:first-child .timetable__table-day--active-list::after {
    content: url("../img/arrow-up.svg"); }
  .timetable__table-day-classes-list li:not(:first-child) {
    font-size: 16px;
    line-height: 16px;
    max-width: none;
    width: 100%;
    padding: 35px 12px; }
  .timetable__table-time-overley {
    display: none;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    opacity: 0.8; }
  .timetable__table-time-overley--active {
    display: block; }
  .timetable__table-day--active-list {
    margin-top: -2px; }
  .timetable__table-day-classes-list--active:first-child li {
    margin-top: 8px; }
  .timetable__table-day--active-list ~ li {
    display: none; } }
